import { NextRequest, NextResponse } from 'next/server';
import { ApiService } from '@/lib/clientApiService';
import type { CompanyOverview } from '@/types/stock';

export async function GET(
  request: NextRequest,
  { params }: { params: { symbol: string } }
): Promise<NextResponse<CompanyOverview | { error: string }>> {
  try {
    const { symbol } = params;
    
    if (!symbol) {
      return NextResponse.json(
        { error: 'Stock symbol is required' },
        { status: 400 }
      );
    }

    const overview = await ApiService.getCompanyOverview(
      symbol.toUpperCase()
    );

    return NextResponse.json(overview);
  } catch (error) {
    console.error('Error fetching company overview:', error);
    return NextResponse.json(
      { error: error instanceof Error ? error.message : 'Failed to fetch company overview' },
      { status: 500 }
    );
  }
}
